  src: url("../fonts/opensans.woff2") format("woff2");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  position: relative;
}

@media (min-width: 768px) {
  .header__nav {
    display: flex;
    width: 87.1%;
    max-width: 1049px;
    flex-wrap: wrap;
    margin-left: auto;
    margin-right: auto;
  }
}
@media (min-width: 1150px) {
