.field-additionally {
  margin: 0 auto;
  width: 81%;

  @media (min-width: $tablet) {
    width: 87%;
  }
}

.field-additionally__title {
  @media (min-width: $tablet) {
    display: flex;
    padding: 50px 0 41px 0;
  }

  @media (min-width: $desktop-width) {
    padding: 0 0 38px 0;
  }
}

.field-additionally__title-name {
  @include font-title(17px, 16px);
  color: $decor-color;
  text-transform: uppercase;
  margin-bottom: 23px;
  display: block;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 8px;
    left: 78px;
    height: 1px;
    width: 200px;
    background-color: #62d1ba;
  }

  @media (min-width: $tablet) {
    margin: 0 128px 0 0;
    padding: 20px 0 0 0;

    &::before {
      display: none;
    }
  }

  @media (min-width: $desktop-width) {
    margin: 0 75px 0 0;
  }
}

.service-form__required-text {
  @include font-title(17px, 16px, 400);
  color: $color-second;
}

.field-additionally__field {
  padding: 15px 0 0 15px;
  width: 100%;
  border: 2px solid $color-second;
  box-sizing: border-box;
  margin-bottom: 15px;

  &::placeholder {
    font-family: "Verdana", sans-serif;

    @include font-title(17px, 18px, 400);
    color: rgb(0, 0, 0, 0.3);

    @media (min-width: $tablet) {
      @include font-title(19px, 18px, 400);
    }
  }

  @media (min-width: $desktop-width) {
    width: 75.4%;
  }

  &:hover {
    border: 2px solid $hover-color;
  }

  &:focus-within {
    outline: 2px solid $hover-color;
    outline-offset: 2px;
  }
}
